.card{
    position: relative;
}
.card img{
    width: 170px;
    height: 170px;
    display: block;
    border: 2px solid #fff;
    border-radius: 6px;
}


/*front of the card the picture*/
.card .front{
    transform: rotateY(90deg);
    transition: all ease-in 0.2s;
    position: absolute;
    background-color: rgb(74, 90, 90);
}

.flipped .front{
    transform:rotateY(0deg);
    transition-delay: 0.2s;
    background-color: rgb(243, 240, 240);
}

/*back of card -- cover */
.card .back{
    transition: all ease-in 0.2s;
    transition-delay: 0.2s;
    cursor: pointer;
}
.flipped .back{
    transform: rotateY(90deg);
    transition-delay: 0s;
}

@media only screen and (max-width: 600px) {
    .card img{
        width: 90px;
        height: 90px;
    }
}

