.App {
  max-width: 860px;
  margin: 20px auto;
}
header {
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: white;
}
h1 {
  font-family:Baskerville,Times,'Times New Roman',serif;
  font-size:2.5em;
  color:#f4f8f0;
  font-variant:small-caps;
  text-align:center;
  font-weight:bold;
}

.App-link {
  color: #61dafb;
}

.card-grid{
  margin-top: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 10px;
}

.btn {
  line-height: 50px;
  height: 50px;
  text-align: center;
  width: 250px;
  cursor: pointer;
}

.btn-one {
  color: #FFF;
  transition: all 0.3s;
  position: relative;
}
.btn-one span {
  font-family:Baskerville,Times,'Times New Roman',serif;
  transition: all 0.3s;
  font-size: 1.3em;
}
.btn-one::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  transition: all 0.3s;
  border-top-width: 1px;
  border-bottom-width: 1px;
  border-top-style: solid;
  border-bottom-style: solid;
  border-top-color: rgba(255,255,255,0.5);
  border-bottom-color: rgba(255,255,255,0.5);
  transform: scale(0.1, 1);
}
.btn-one:hover span {
  letter-spacing: 2px;
}
.btn-one:hover::before {
  opacity: 1; 
  transform: scale(1, 1); 
}
.btn-one::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  transition: all 0.3s;
  background-color: rgba(255,255,255,0.1);
}
.btn-one:hover::after {
  opacity: 0; 
  transform: scale(0.1, 1);
}

footer{
  display: flex;
  justify-content: space-around;
}
p{
  font-size: 2em;
  margin-top: 5px;
}

@media only screen and (max-width: 600px) {
  header{
    flex-direction: column;
    margin-bottom: 10px;
  }
      h1{
        font-size: 1.5em;
      }
      .btn {
        line-height: 40px;
        height: 40px;
        text-align: center;
        width: 200px;
        cursor: pointer;
      }
      p{
        font-size: 0.9em;
      }
}

